export const STABLE_LAYER_NETWORK = 'mainnet' as const;

export const USDC_TYPE =
    '0xdba34672e30cb065b1f93e3ab55318768fd6fef66c15942c9f7cb846e2f900e7::usdc::USDC';
export const BTC_USDC_TYPE =
    '0x6d9fc33611f4881a3f5c0cd4899d95a862236ce52b3a38fef039077b0c5b5834::btc_usdc::BtcUSDC';

export const LUCKY_MONEY_PACKAGE_ID =
    '0xbb6828ba12f04b843647d013601f166dec59dcfd061e8230dd59977892dd4f46';
export const LUCKY_MONEY_REGISTRY_ID =
    '0xec0b205973baa7b6bea59967ac8c734a5b2b4defb34a262dff6507c3457d5e6f';

export const SUI_CLOCK_OBJECT_ID = '0x6';
export const SUI_RANDOM_OBJECT_ID = '0x8';

export const BTC_USDC_DECIMALS = 6;

export const isPlaceholderObjectId = (value: string) => /^0x0+$/i.test(value);

export const isLuckyMoneyConfigReady = () =>
    !isPlaceholderObjectId(LUCKY_MONEY_PACKAGE_ID) &&
    !isPlaceholderObjectId(LUCKY_MONEY_REGISTRY_ID);

export const parseTypeArgument = (fullType: string) => {
    const match = fullType.match(/<(.+)>/);
    return match?.[1] ?? null;
};

export const getCoinSymbolFromType = (coinType: string) => {
    const parts = coinType.split('::');
    return parts[parts.length - 1] || coinType;
};
