import { useNavigate } from '@modern-js/runtime/router';
import { motion } from 'framer-motion';
import { LeftOutlined } from '@ant-design/icons';
import { ConnectButton } from '@mysten/dapp-kit';

interface RedHeaderProps {
    title?: string;
    showBack?: boolean;
    showWallet?: boolean;
}

/**
 * 页面头部 — 暖白透明风格 + 刘海避让
 *
 * 分为两层:
 *   1) 顶部空白 — 高度 = var(--lm-notch-height)，留出刘海区域
 *   2) 实际 header — 44px 高，含返回键 + 标题
 */
export default function RedHeader({
    title = '',
    showBack = true,
    showWallet = true,
}: RedHeaderProps) {
    const navigate = useNavigate();

    return (
        <div
            style={{
                position: 'sticky',
                top: 0,
                zIndex: 50,
                background: 'rgba(255, 250, 245, 0.92)',
                backdropFilter: 'blur(16px)',
                WebkitBackdropFilter: 'blur(16px)',
                borderBottom: '1px solid rgba(0, 0, 0, 0.04)',
            }}
        >
            {/* 刘海占位 */}
            <div style={{ height: 'var(--lm-notch-height)' }} />

            {/* 实际 header 内容 */}
            <div
                style={{
                    display: 'grid',
                    gridTemplateColumns: '36px 1fr auto',
                    alignItems: 'center',
                    height: 44,
                    padding: '0 12px',
                    gap: 8,
                }}
            >
                {showBack && (
                    <motion.button
                        whileTap={{ scale: 0.9 }}
                        onClick={() => navigate(-1)}
                        style={{
                            width: 32,
                            height: 32,
                            borderRadius: '50%',
                            background: 'rgba(212, 56, 13, 0.06)',
                            border: '1px solid rgba(212, 56, 13, 0.08)',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            cursor: 'pointer',
                            color: 'var(--lm-red)',
                            fontSize: 13,
                            flexShrink: 0,
                        }}
                    >
                        <LeftOutlined />
                    </motion.button>
                )}
                {!showBack && <div style={{ width: 32, height: 32 }} />}

                {title && (
                    <h2
                        style={{
                            textAlign: 'center',
                            fontSize: 16,
                            fontWeight: 600,
                            color: 'var(--lm-text-primary)',
                            margin: 0,
                            letterSpacing: 0.5,
                        }}
                    >
                        {title}
                    </h2>
                )}
                {!title && <div />}

                {showWallet ? (
                    <div style={{ justifySelf: 'end' }}>
                        <ConnectButton
                            connectText="连接钱包"
                            style={{
                                height: 30,
                                padding: '0 10px',
                                borderRadius: 16,
                                border: '1px solid rgba(212,56,13,0.12)',
                                background: 'rgba(212,56,13,0.06)',
                                color: 'var(--lm-red)',
                                fontSize: 12,
                                fontWeight: 600,
                                lineHeight: '30px',
                                cursor: 'pointer',
                            }}
                        />
                    </div>
                ) : (
                    <div style={{ width: 88 }} />
                )}
            </div>
        </div>
    );
}
