import React, { useEffect, useMemo, useState } from 'react';
import { useNavigate } from '@modern-js/runtime/router';
import { motion } from 'framer-motion';
import {
    WalletOutlined,
    ArrowDownOutlined,
    ArrowUpOutlined,
    UndoOutlined,
    GiftOutlined,
    StarFilled,
} from '@ant-design/icons';
import { useCurrentAccount, useSuiClient } from '@mysten/dapp-kit';
import PageContainer from '../../components/PageContainer';
import RedHeader from '../../components/RedHeader';
import { LUCKY_COLORS } from '../../theme/themeConfig';
import {
    BTC_USDC_DECIMALS,
    BTC_USDC_TYPE,
    USDC_TYPE,
    isLuckyMoneyConfigReady,
} from '../../config/luckyMoney';

type RecordType = 'received' | 'sent' | 'refunded';

interface AssetRecord {
    id: string;
    amount: number;
    type: RecordType;
    timestamp: string;
    from?: string;
}

interface AssetSummary {
    envelopeCount: number;
    totalReceived: number;
    totalSent: number;
}

const sleep = (ms: number) =>
    new Promise(resolve => {
        setTimeout(resolve, ms);
    });

const parseObjectId = (value: any): string => {
    if (typeof value === 'string') return value;
    if (value && typeof value === 'object') {
        if (typeof value.id === 'string') return value.id;
        if (typeof value.bytes === 'string') {
            return value.bytes.startsWith('0x') ? value.bytes : `0x${value.bytes}`;
        }
    }
    return '';
};

const toAmount = (raw: unknown, decimals = BTC_USDC_DECIMALS) => {
    try {
        const value = BigInt(String(raw ?? '0'));
        return Number(value) / 10 ** decimals;
    } catch {
        return 0;
    }
};

const parseRecordsFromTx = (
    tx: any,
    accountAddress: string,
): AssetRecord[] => {
    const events = tx?.events;
    if (!Array.isArray(events)) return [];

    const records: AssetRecord[] = [];
    const timestamp = tx?.timestampMs
        ? new Date(Number(tx.timestampMs)).toISOString()
        : new Date().toISOString();

    for (let i = 0; i < events.length; i++) {
        const evt = events[i];
        const eventType = evt?.type;
        const parsed = evt?.parsedJson ?? {};

        if (typeof eventType !== 'string') continue;

        if (eventType.endsWith('::events::LuckyMoneyCreated')) {
            records.push({
                id: `${tx.digest}-created-${i}`,
                amount: toAmount(parsed.total_amount),
                type: 'sent',
                timestamp,
                from: parseObjectId(parsed.lucky_money_id),
            });
            continue;
        }

        if (eventType.endsWith('::events::LuckyMoneyClaimed')) {
            const claimer = String(parsed.claimer ?? '').toLowerCase();
            if (claimer === accountAddress.toLowerCase()) {
                records.push({
                    id: `${tx.digest}-claimed-${i}`,
                    amount: toAmount(parsed.amount),
                    type: 'received',
                    timestamp,
                    from: parseObjectId(parsed.lucky_money_id),
                });
            }
            continue;
        }

        if (eventType.endsWith('::events::LuckyMoneyRefunded')) {
            const owner = String(parsed.owner ?? '').toLowerCase();
            if (owner === accountAddress.toLowerCase()) {
                records.push({
                    id: `${tx.digest}-refund-${i}`,
                    amount: toAmount(parsed.refunded_amount),
                    type: 'refunded',
                    timestamp,
                    from: parseObjectId(parsed.lucky_money_id),
                });
            }
        }
    }

    return records;
};

export default function AssetsPage() {
    const navigate = useNavigate();
    const account = useCurrentAccount();
    const suiClient = useSuiClient();

    const [loading, setLoading] = useState(true);
    const [usdcBalance, setUsdcBalance] = useState(0);
    const [btcUsdcBalance, setBtcUsdcBalance] = useState(0);
    const [records, setRecords] = useState<AssetRecord[]>([]);
    const [summary, setSummary] = useState<AssetSummary>({
        envelopeCount: 0,
        totalReceived: 0,
        totalSent: 0,
    });

    useEffect(() => {
        let active = true;

        const fetchAssetData = async () => {
            if (!account) {
                if (!active) return;
                setUsdcBalance(0);
                setBtcUsdcBalance(0);
                setRecords([]);
                setSummary({ envelopeCount: 0, totalReceived: 0, totalSent: 0 });
                setLoading(false);
                return;
            }

            try {
                setLoading(true);

                const [usdc, btcUsdc] = await Promise.all([
                    suiClient.getBalance({ owner: account.address, coinType: USDC_TYPE }),
                    suiClient.getBalance({ owner: account.address, coinType: BTC_USDC_TYPE }),
                ]);

                if (!active) return;

                setUsdcBalance(toAmount(usdc.totalBalance));
                setBtcUsdcBalance(toAmount(btcUsdc.totalBalance));

                if (!isLuckyMoneyConfigReady()) {
                    setRecords([]);
                    setSummary({ envelopeCount: 0, totalReceived: 0, totalSent: 0 });
                    setLoading(false);
                    return;
                }

                const txResult = await suiClient.queryTransactionBlocks({
                    filter: { FromAddress: account.address },
                    options: {
                        showEvents: true,
                    },
                    order: 'descending',
                    limit: 40,
                });

                if (!active) return;

                const parsed = txResult.data
                    .flatMap(tx => parseRecordsFromTx(tx, account.address))
                    .sort(
                        (a, b) =>
                            new Date(b.timestamp).getTime() -
                            new Date(a.timestamp).getTime(),
                    );

                const totalReceived = parsed
                    .filter(r => r.type === 'received')
                    .reduce((sum, r) => sum + r.amount, 0);
                const totalSent = parsed
                    .filter(r => r.type === 'sent')
                    .reduce((sum, r) => sum + r.amount, 0);
                const envelopeCount = parsed.filter(r => r.type === 'sent').length;

                setRecords(parsed.slice(0, 20));
                setSummary({ envelopeCount, totalReceived, totalSent });
                setLoading(false);
            } catch (e) {
                console.error('Failed to load asset data', e);
                if (!active) return;
                setLoading(false);
            }
        };

        fetchAssetData();

        const timer = setInterval(async () => {
            await sleep(0);
            fetchAssetData();
        }, 15000);

        return () => {
            active = false;
            clearInterval(timer);
        };
    }, [account, suiClient]);

    const totalBalance = useMemo(() => usdcBalance + btcUsdcBalance, [usdcBalance, btcUsdcBalance]);

    const iconMap: Record<RecordType, React.ReactNode> = {
        received: <ArrowDownOutlined style={{ fontSize: 16, color: '#52C41A' }} />,
        sent: <ArrowUpOutlined style={{ fontSize: 16, color: LUCKY_COLORS.red500 }} />,
        refunded: <UndoOutlined style={{ fontSize: 16, color: LUCKY_COLORS.gold500 }} />,
    };
    const labelMap: Record<RecordType, string> = {
        received: '收到红包',
        sent: '发出红包',
        refunded: '退回',
    };

    const friendlyTime = (iso: string) => {
        const diff = Date.now() - new Date(iso).getTime();
        const min = Math.floor(diff / 60000);
        if (min < 60) return `${Math.max(min, 1)} 分钟前`;
        const hr = Math.floor(min / 60);
        if (hr < 24) return `${hr} 小时前`;
        return `${Math.floor(hr / 24)} 天前`;
    };

    return (
        <PageContainer>
            <RedHeader title="我的资产" />

            <div style={{ padding: '8px 20px 100px' }}>
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    style={{
                        background: LUCKY_COLORS.gradientRedSoft,
                        borderRadius: 20,
                        padding: '28px 24px 20px',
                        marginBottom: 28,
                        boxShadow: '0 8px 32px rgba(212, 56, 13, 0.15)',
                        color: '#FFFFFF',
                    }}
                >
                    <div
                        style={{
                            display: 'flex',
                            alignItems: 'center',
                            gap: 6,
                            fontSize: 13,
                            opacity: 0.85,
                            marginBottom: 12,
                        }}
                    >
                        <WalletOutlined />
                        总资产
                    </div>
                    <div style={{ display: 'flex', alignItems: 'baseline', gap: 6 }}>
                        <span
                            style={{
                                fontSize: 38,
                                fontWeight: 900,
                                letterSpacing: -1,
                            }}
                        >
                            {account ? totalBalance.toFixed(2) : '--'}
                        </span>
                        <span
                            style={{
                                fontSize: 14,
                                fontWeight: 500,
                                opacity: 0.75,
                            }}
                        >
                            USDC
                        </span>
                    </div>

                    <div
                        style={{
                            display: 'flex',
                            marginTop: 20,
                            paddingTop: 16,
                            borderTop: '1px solid rgba(255,255,255,0.2)',
                        }}
                    >
                        {[
                            { label: '红包数量', value: account ? summary.envelopeCount : '--' },
                            {
                                label: '累计收到',
                                value: account
                                    ? `+${summary.totalReceived.toFixed(2)}`
                                    : '--',
                                color: '#FFFBE6',
                            },
                            {
                                label: '累计发出',
                                value: account
                                    ? `-${summary.totalSent.toFixed(2)}`
                                    : '--',
                                color: 'rgba(255,255,255,0.6)',
                            },
                        ].map(item => (
                            <div key={item.label} style={{ flex: 1, textAlign: 'center' }}>
                                <div
                                    style={{
                                        fontSize: 18,
                                        fontWeight: 700,
                                        color: (item as any).color || '#FFFFFF',
                                    }}
                                >
                                    {item.value}
                                </div>
                                <div
                                    style={{
                                        fontSize: 11,
                                        opacity: 0.7,
                                        marginTop: 2,
                                    }}
                                >
                                    {item.label}
                                </div>
                            </div>
                        ))}
                    </div>
                </motion.div>

                <motion.div
                    initial={{ opacity: 0, y: 10 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.25 }}
                    whileTap={{ scale: 0.98 }}
                    onClick={() => navigate('/wishlist')}
                    style={{
                        background: LUCKY_COLORS.pureWhite,
                        borderRadius: 14,
                        padding: '14px 18px',
                        marginBottom: 24,
                        display: 'flex',
                        alignItems: 'center',
                        gap: 12,
                        cursor: 'pointer',
                        border: '1.5px solid rgba(212,56,13,0.1)',
                        boxShadow: '0 2px 8px rgba(212,56,13,0.04)',
                    }}
                >
                    <StarFilled style={{ fontSize: 20, color: LUCKY_COLORS.red500 }} />
                    <div style={{ flex: 1 }}>
                        <span style={{ fontSize: 13, color: LUCKY_COLORS.textSecondary }}>
                            心愿单余额
                        </span>
                    </div>
                    <span style={{ fontSize: 16, fontWeight: 700, color: LUCKY_COLORS.red500 }}>
                        0.00
                    </span>
                    <span style={{ fontSize: 12, color: LUCKY_COLORS.textHint }}>USDC ›</span>
                </motion.div>

                <h3
                    style={{
                        fontSize: 16,
                        fontWeight: 700,
                        color: LUCKY_COLORS.textPrimary,
                        margin: '0 0 16px',
                    }}
                >
                    交易记录
                </h3>

                {!account && (
                    <div
                        style={{
                            background: LUCKY_COLORS.pureWhite,
                            borderRadius: 14,
                            padding: '16px',
                            color: LUCKY_COLORS.textHint,
                            fontSize: 13,
                            border: '1px solid rgba(0,0,0,0.03)',
                            boxShadow: 'var(--lm-shadow-card)',
                        }}
                    >
                        请先连接钱包查看链上资产与记录。
                    </div>
                )}

                {account && loading && (
                    <div
                        style={{
                            background: LUCKY_COLORS.pureWhite,
                            borderRadius: 14,
                            padding: '16px',
                            color: LUCKY_COLORS.textHint,
                            fontSize: 13,
                            border: '1px solid rgba(0,0,0,0.03)',
                            boxShadow: 'var(--lm-shadow-card)',
                        }}
                    >
                        正在加载链上记录...
                    </div>
                )}

                {account && !loading && records.length === 0 && (
                    <div
                        style={{
                            background: LUCKY_COLORS.pureWhite,
                            borderRadius: 14,
                            padding: '16px',
                            color: LUCKY_COLORS.textHint,
                            fontSize: 13,
                            border: '1px solid rgba(0,0,0,0.03)',
                            boxShadow: 'var(--lm-shadow-card)',
                        }}
                    >
                        暂无链上红包交易记录。
                    </div>
                )}

                {account && records.length > 0 && (
                    <div style={{ display: 'flex', flexDirection: 'column', gap: 10 }}>
                        {records.map((record, i) => (
                            <motion.div
                                key={record.id}
                                initial={{ opacity: 0, y: 14 }}
                                animate={{ opacity: 1, y: 0 }}
                                transition={{ delay: i * 0.04 }}
                                style={{
                                    background: LUCKY_COLORS.pureWhite,
                                    borderRadius: 14,
                                    padding: '14px 16px',
                                    display: 'flex',
                                    alignItems: 'center',
                                    gap: 12,
                                    boxShadow: 'var(--lm-shadow-card)',
                                    border: '1px solid rgba(0,0,0,0.03)',
                                }}
                            >
                                <div
                                    style={{
                                        width: 40,
                                        height: 40,
                                        borderRadius: 12,
                                        background:
                                            record.type === 'received'
                                                ? 'rgba(82, 196, 26, 0.08)'
                                                : record.type === 'sent'
                                                    ? LUCKY_COLORS.red50
                                                    : LUCKY_COLORS.gold50,
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                        flexShrink: 0,
                                    }}
                                >
                                    {iconMap[record.type]}
                                </div>

                                <div style={{ flex: 1, minWidth: 0 }}>
                                    <div
                                        style={{
                                            fontSize: 14,
                                            fontWeight: 600,
                                            color: LUCKY_COLORS.textPrimary,
                                        }}
                                    >
                                        {labelMap[record.type]}
                                    </div>
                                    <div
                                        style={{
                                            fontSize: 12,
                                            color: LUCKY_COLORS.textHint,
                                            marginTop: 2,
                                        }}
                                    >
                                        {friendlyTime(record.timestamp)}
                                        {record.from && ` · ${record.from.slice(0, 8)}...`}
                                    </div>
                                </div>

                                <span
                                    style={{
                                        fontSize: 16,
                                        fontWeight: 700,
                                        color:
                                            record.type === 'received'
                                                ? '#52C41A'
                                                : record.type === 'sent'
                                                    ? LUCKY_COLORS.red500
                                                    : LUCKY_COLORS.gold600,
                                        flexShrink: 0,
                                    }}
                                >
                                    {record.type === 'sent' ? '-' : '+'}
                                    {record.amount.toFixed(2)}
                                </span>
                            </motion.div>
                        ))}
                    </div>
                )}
            </div>

            <div
                style={{
                    position: 'sticky',
                    bottom: 0,
                    padding: '16px 20px calc(16px + var(--lm-safe-bottom))',
                    background: 'rgba(255, 250, 245, 0.9)',
                    backdropFilter: 'blur(12px)',
                    WebkitBackdropFilter: 'blur(12px)',
                }}
            >
                <motion.button
                    whileHover={{ scale: 1.02 }}
                    whileTap={{ scale: 0.98 }}
                    onClick={() => navigate('/create')}
                    style={{
                        width: '100%',
                        height: 52,
                        borderRadius: 26,
                        background: LUCKY_COLORS.gradientRedSoft,
                        border: 'none',
                        color: '#FFFFFF',
                        fontSize: 16,
                        fontWeight: 700,
                        cursor: 'pointer',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        gap: 8,
                        letterSpacing: 2,
                        boxShadow: '0 6px 24px rgba(212, 56, 13, 0.2)',
                    }}
                >
                    <GiftOutlined />
                    发红包
                </motion.button>
            </div>
        </PageContainer>
    );
}
